#services {
    background-color: #191818;
    padding-top: 5rem;
    width: 100%;

    .container {
        width: 90%;
        padding-bottom: 7rem;

        .services-title {
            color: #dddbdb;
            background-color: #191818;
            letter-spacing: 6px;
            font-size: 46px;
            padding-bottom: 2rem;
            font-weight: 300;
            text-align: center;
            text-transform: uppercase;
        }

        .services-top {
            justify-content: space-around;
            padding: 14px 20px;
            background: #2c2b2b;
            border-radius: 25px 15px 0 0;
            align-items: center;
            display: flex;
            overflow-x: scroll;

            .top-circles {
                display: flex;
                align-items: center;
                gap: 10px;

                .dot {
                    width: 15px;
                    height: 15px;
                    color: #f7f7f7;
                    border: 1px solid #fff;
                    border-radius: 50%;
                    display: flex;
                }
            }

            .services_top_btns {
                display: flex;
                margin-left: 20px;
                gap: 15px;
                cursor: pointer;
                align-items: center;

                .services_top_btn {
                    padding: 8px 22px;
                    margin-right: 8px;
                    border: 1px solid #ddd;
                    border-radius: 38px;
                    font-weight: 500;
                }

                span.active {
                    color: #242424;
                    background: #fff;
                }
            }
        }

        .services-main {
            height: auto;
            border: 1px solid #979494;
            background-color: #3d3b3b;
            overflow: hidden;
            display: flex;
            padding-top: 3rem;
            padding-bottom: 3rem;

            .services-item {

                .services__main--item__text {
                    display: block;
                    padding-left: 50px;

                    .services__main--item__text-title {
                        font-weight: 600;
                        font-size: 24px;
                        line-height: 1.3;
                    }

                    .services__main--item__text-desc {
                        margin-top: 15px;
                        font-size: 16px;
                        line-height: 1.3;
                    }

                    .services__main--item__text-pre-title {
                        margin-top: 30px;
                        font-weight: 500;
                        font-size: 20px;
                        padding-bottom: 10px;
                    }

                    .services-main_list {
                        display: flex;
                        gap: 12px;
                        margin-top: 15px;
                        max-width: 600px;
                        display: flex;
                        margin-bottom: 10px;
                        align-items: center;
                    }

                    .btn {
                        margin-top: 30px;
                        width: 45%;
                        padding: 12px 20px;
                    }
                }
            }
        }

        .rightside {
            position: relative;
            margin-left: 25px;

            .services-img {
                width: 700px;
                height: 400px;
            }
        }
    }

}

@media (max-width: 1200.98px) {

    #services .container {

        .services-title {
            font-size: 36px;
        }

        .services-top {

            .top-circles .dot {
                display: none;
            }

            .services_top_btns .services_top_btn .rightside .services-img {
                width: 100%;
            }
        }
    }
}

@media (max-width: 970.98px) {

    #services .container {

        .services-top {

            .top-circles .dot {
                display: none;
            }

            .services_top_btns .services_top_btn {
                width: 100%;
            }
        }

        .services-main {

            .services-item .services-main_list {
                margin-top: 10px;
                padding-left: 10px;

            }

            .rightside .services-img {
                display: none;
            }
        }
    }
}

@media (max-width: 479.98px) {

    #services .container {

        .services-title {
            font-size: 25px;
        }

        .services-main {

            .services-item .services__main--item__text {
                padding-left: 30px;
                font-size: 15px;
            }

            .btn {
                font-size: 15px;
                font-weight: 500;
                letter-spacing: 1px;
            }
        }
    }
}