@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,400;1,500;1,600;1,700;1,900&display=swap');

* {
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 100%;
  color: rgb(247, 250, 250);
  font-family: 'Montserrat';
}

body {
  background-color: #191818;
}
li,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: rgb(204, 204, 204);
}
