#main {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    background-color: #191818;

    .container {
        margin-top: 7rem;
        margin-bottom: 5rem;
        width: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        .main-text {
            font-weight: 200;
            font-size: 66px;
        }

        .main-text span {
            font-weight: 600;
        }

        .button {
            margin-top: 6rem;
            width: 100%;
        }
    }
}

@media (max-width: 1024.98px) {
    #main {

        .container {
            margin-top: 12rem;


            .main-text {
                font-size: 60px;

                span {
                    font-weight: 600;
                }
            }
        }
    }
}


@media (max-width: 679.98px) {
    #main {

        .container {
            margin-top: 12rem;


            .main-text {
                font-size: 40px;
            }

            .main-text span {
                font-weight: 600;
            }

            .button {
                margin-top: 50px;

                .btn {
                    padding: 13px 30px;
                    font-size: 10px;
                }
            }
        }
    }
}

@media (max-width: 479.98px) {
    #main {

        .container {
            margin-top: 15rem;

            .main-text {
                font-size: 33px;
            }

            .main-text span {
                font-weight: 600;
            }

            .button {
                margin-top: 40px;

                .btn {
                    padding: 10px 20px;
                    font-size: 13px;
                    letter-spacing: 1px;
                }
            }
        }
    }
}